<template>
    <div style="text-align: center">
        <p style="font-size: 20px ; width:100%;height: 60px;display: block;line-height: 60px;text-align: center">添加数据源</p>
        <el-form label-position="left" label-width="140px" :model="formDataSourceInfo">
            <el-form-item label="数据源类型">
                <el-select v-model="formDataSourceInfo.dataSourceType" placeholder="请选择类型">
                    <el-option label="mysql" value="mysql"></el-option>
                    <el-option label="sqlServer" value="sqlServer"></el-option>
                    <el-option label=".xml" value="xml"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="数据源编号">
                <el-input v-model="formDataSourceInfo.dataSourceId"></el-input>
            </el-form-item>
            <el-form-item label="数据源版本">
                <el-input v-model="formDataSourceInfo.dataSourceVersion"></el-input>
            </el-form-item>
            <el-form-item label="数据源名称">
                <el-input v-model="formDataSourceInfo.dataSourceName" ></el-input>
            </el-form-item>
            <el-form-item label="数据源别名">
                <el-input v-model="formDataSourceInfo.dataSourceAlias" ></el-input>
            </el-form-item>
            <el-form-item label="数据源IP地址">
                <el-input v-model="formDataSourceInfo.dataSourceIP" ></el-input>
            </el-form-item>
            <el-form-item label="数据源端口号">
                <el-input v-model="formDataSourceInfo.dataSourcePort" ></el-input>
            </el-form-item>
            <el-form-item label="数据源登录用户名">
                <el-input v-model="formDataSourceInfo.dataSourceLoginName" ></el-input>
            </el-form-item>
            <el-form-item label="数据源登录密码">
                <el-input v-model="formDataSourceInfo.dataSourceLoginPwd" ></el-input>
            </el-form-item>
            <el-form-item label="额外信息">
                <el-input v-model="formDataSourceInfo.dataSourceExtraInfo" ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit">添加</el-button>
                <el-button>取消</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import axios from "axios";
    /*import qs from "qs";qs.stringify(*/

    export default {
        name: "AddDataSource",
        data () {
            return {
                formDataSourceInfo:{
                    //数据源编号
                    dataSourceId:'',
                    // 数据源类型
                    dataSourceType:'',
                    // 数据源版本
                    dataSourceVersion:'',
                    // 数据源名称
                    dataSourceName: '',
                    //  数据源别名
                    dataSourceAlias: '',
                    // IP地址
                    dataSourceIP: '',
                    // 数据源端口号
                    dataSourcePort:'',
                    //  数据源登录用户名
                    dataSourceLoginName:'',
                    //  数据源登录密码
                    dataSourceLoginPwd:'',
                    //  额外信息
                    dataSourceExtraInfo:''
                }
            }
        },
        methods: {
            onSubmit () {
                console.log(this.formDataSourceInfo)
                if(this.formDataSourceInfo.dataSourceId==='')
                {
                    alert("数据源编号不能为空")
                    return
                }
                axios.post('http://localhost:8888/addDataSourceInfo',this.formDataSourceInfo)
                    .then((response)=>{
                            console.log(response);
                        alert("添加数据源信息成功")
                        }
                    )
            }
        }
    }
</script>

<style scoped>

</style>
